import './AboutMe.scss';
import AboutMePhoto from '../../img/about-me.jpg';

const AboutMe: React.FC = () => {
  return (
    <div className="about-me-section">
      <div className="photo-section">
        <div className="photo">
          <img className="about-me-photo" src={AboutMePhoto} alt="harmony" />
          <div className="tile-info">
            <div className="name">mgr DANUTA SZUMISKA</div>
            <div>
              Terapeutka BICOM oraz Biophilia Tracker
              <br />
              nauczycielka wychowania fizycznego
              <br />
              trenerka personalna, instruktorka fitness
            </div>
          </div>
        </div>
        <div className="text-section">
          <div className="name">KILKA SW O MNIE</div>
          <div>
            <div className="text">
              Jestem specjalistk zajmujca si diagnoz oraz terapi za pomoc
              zaawansowanych urzdze biorezonansu. Moja pasja do medycyny
              niekonwencjonalnej, zdrowego stylu ycia i aktywnoci fizycznej
              skonia mnie do pogbiania wiedzy przez studia oraz liczne
              kursy, dziki ktrym zdobyam umiejtnoci patrzenia na pacjenta w
              sposb holistyczny. Uwzgldniam wszystkie aspekty ycia, ktre
              mog wpywa na zdrowie i dobre samopoczucie.
            </div>
            <div className="text">
              Popraw zdrowia moich pacjentw daje mi niesamowit satysfakcj i
              utwierdza w przekonaniu, e nasz organizm ma ogromne zdolnoci do
              samouzdrawiania, jedynym czego potrzebuje jest impuls i
              zainicjowanie odpowiednich warunkw ku temu.
            </div>
            <div className="text">
              Celem mojej pracy jest przywrcenie organizmowi odpowiednich
              warunkw do regeneracji i poprawnego funkcjonowania. Jestem
              ukierunkowana na postawienie waciwej diagnozy, dotarcie do
              rda problemu i wyeliminowanie go. Podchodz indywidualnie do
              kadego pacjenta, co prowadzi do spersonalizowanej i skutecznej
              terapii.
            </div>
            <div className="text">
              Zapraszam Ci do mojego gabinetu biorezonansu, gdzie wsplnie
              zadbamy o Twoje zdrowie i harmoni.
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AboutMe;
