import './Home.scss';
import HomePhoto from '../../img/home-photo.jpg';
import HomePhoto2 from '../../img/home-photo-2.jpg';

const Home: React.FC = () => {
  return (
    <>
      <div className="section-1">
        <img className="home-photo" src={HomePhoto} alt="harmony" />
        <div className="theme-line-container text-container">
          <div className="theme-line">
            <div>ENERGIA I ZDROWIE W HARMONII </div>
            <div>Z BIOREZONANSEM</div>
          </div>
          <div className="theme-line-text theme-line-content">
            Terapia biorezonansem moe by wykorzystywana w wielu obszarach
            zdrowia, od wsparcia w leczeniu dolegliwoci fizycznych, po redukcj
            stresu i popraw samopoczucia. Wspiera organizm w eliminacji toksyn,
            redukcji stanw zapalnych i przywrceniu naturalnej rwnowagi
            energetycznej.
          </div>
        </div>
      </div>
      <div className="section-2">
        <div className="text-container">
          <div className="theme-line-title">DIAGNOSTYKA</div>
          <div className="theme-line-content content">
            Diagnostyka biorezonansem to innowacyjna metoda badania, ktra
            pozwala na analiz energii i informacji obecnych w organizmie.
            Pozwala wykry ewentualne zaburzenia energetyczne czy
            nieprawidowoci w funkcjonowaniu rnych narzdw i ukadw.
          </div>
          <div className="theme-line-title">TERAPIA</div>
          <div className="theme-line-content content">
            Terapia skupia si na harmonizowaniu i wzmocnieniu energetycznego
            pola ciaa. Poprzez analiz i optymalizacj rezonansu
            elektromagnetycznego, terapia biorezonansem moe pomc w wykrywaniu
            i redukcji nieprawidowoci energetycznych, ktre mog przyczynia
            si do problemw zdrowotnych.
          </div>
          <div className="theme-line-title">
            KOMPLEKSOWY PROGRAM <br />
            ZRWNOWAONEGO ODCHUDZANIA
          </div>

          <div className="theme-line-content content">
            Programy odchudzania z indywidualnym podejciem, ktry czy terapi
            biorezonansem z planem ywieniowym i aktywnoci fizyczn.
          </div>
        </div>
        <img className="home-photo-2" src={HomePhoto2} alt="harmony" />
      </div>
    </>
  );
};

export default Home;
