import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Typography,
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

const FAQ = () => {
  const faqData = [
    {
      question: 'Ile trwa wizyta?',
      answer:
        'Czas trwania wizyty uzaleniony jest od jej celu. Zwykle sama sesja terapeutyczna zajmuje okoo 1 godziny. W przypadku przeprowadzenia kompleksowego skanu caego organizmu, wizyta moe potrwa nawet okoo 2 godzin.',
    },
    {
      question: 'Jak przygotowa si do wizyty?',
      answer:
        'Na wizyt naley przyj odpowiednio nawodnionym (zalecam spoycie okoo 1,5 litra wody). Na 2 godziny przed badaniem nie spoywa posikw oraz nie pi kawy. W przypadku testw alergicznych naley odstawi leki antyhistaminowe (jeli nie ma przeciwwskaza zdrowotnych) i wapno na okres przynajmniej 2 tygodni.',
    },
    {
      question: 'Czy biorezonans jest bezpieczny?',
      answer:
        'Terapie biorezansowe s w peni bezpieczne. W wyniku niszczenia patogenw poprzez czstotliwoci, moe czasem doj do reakcji Herxheimera, czyli do nasilenia si objaww z powodu uwolnienia do krwi produktw przemiany materii oraz toksyn z niszczonych patogenw. Charakterystyczne dla intoksykacji s ble gowy, dusznoci, ble w miniach i stawach, zawroty gowy, wysypki, sabo lub senno.  Warto stosowa si do rad terapeuty, ktre mog zapobiec wyej wymienionym reakcjom.',
    },
    {
      question: 'Jakie s przeciwwskazania?',
      answer:
        'Przeciwwskazaniem do terapii jest posiadanie rozrusznika serca, implantu elektronicznego, bd cia.',
    },
    {
      question: 'Jaka jest skuteczno?',
      answer:
        'Odpowiednio prowadzona terapia prowadzona na waciwie skalibrowanym, nowoczesnym sprzcie w wikszoci przypadkw daje pozytywne, widoczne przez samego pacjenta efekty. Przebieg terapii, w zalenoci od schorzenia i osoby moe wyglda nieco inaczej. U jednych nastpuje cakowite pozbycie si alergii/obcienia ju po pierwszych 2-3 wizytach, u innych konieczne jest powtrzenie terapii.',
    },
    {
      question: 'Czy ta metoda jest wiarygodna?',
      answer:
        'Biorezonans jest rozpowszechniony w wielu krajach Europy Zachodniej, jako skuteczna, nieinwazyjna i niedajca skutkw ubocznych terapia, niejednokrotnie finansowana z narodowych funduszy zdrowia. Biorezonans jest ju rwnie na wyposaeniu niektrych szpitali.',
    },
    {
      question:
        'Czy biorezonans moe by stosowany jako metoda wspomagajca leczenie tradycyjne?',
      answer:
        'Biorezonans moe by stosowany jako metoda wspomagajca terapie konwencjonalne, pomagajc w poprawie reakcji organizmu na leczenie.',
    },
    {
      question: 'Czy biorezonans jest odpowiedni dla dzieci?',
      answer:
        'Przed przystpieniem do sesji biorezonansu dla dzieci, zawsze zalecamy skonsultowanie si z lekarzem rodzinnym lub pediatr. Lekarz moe oceni, czy biorezonans jest odpowiedni w kontekcie zdrowia i potrzeb Twojego dziecka.',
    },
    {
      question:
        'Jak czsto powinno si przeprowadza sesje biorezonansu, aby osign podane efekty?',
      answer:
        'Czstotliwo sesji biorezonansu moe si rni w zalenoci od kilku czynnikw, takich jak cel terapii, stan zdrowia, indywidualne potrzeby pacjenta oraz zalecenia specjalisty pracujcego z biorezonansem. Czstotliwo moe by dostosowana w miar postpw i odpowiedzi organizmu na terapi. Wane jest rwnie, aby dawa organizmowi czas na reakcj na zmiany energetyczne wprowadzane przez biorezonans.',
    },
    {
      question:
        'Czy biorezonans moe pomc w redukcji stresu lub poprawie oglnego samopoczucia?',
      answer:
        'Tak, biorezonans moe by uywany jako metoda, ktra ma potencjalnie pomc w redukcji stresu oraz poprawie oglnego samopoczucia. Chocia biorezonans jest czsto postrzegany jako narzdzie diagnostyczne, istnieje przekonanie, e wpywa on rwnie na energetyczn rwnowag organizmu, co moe przyczyni si do agodzenia stresu i poprawy komfortu psychicznego. ',
    },
    {
      question: 'Czy biorezonans moe pomc w poprawie jakoci snu?',
      answer:
        'Biorezonans moe pomc w redukcji napicia i stresu, co moe przyczyni si do atwiejszego zasypiania i lepszego snu.',
    },
    {
      question: 'Dlaczego BioStrefa to wyjtkowy gabinet?',
      answer:
        'Jako jedyny gabinet w Poznaniu oferuj Pastwu unikaln diagnoz oraz terapi opart na dwch zaawansowanych urzdzeniach, ktre s uwaane za jedne z najbardziej precyzyjnych i skutecznych na wiecie. Pierwszym z nich jest Bicom renomowanej niemieckiej firmy REGUMED GmbH, zapewniajcy niezrwnan jako diagnostyki i terapii. Drugim jest Biophilia Tracker X4, ktry w poczeniu z Bicom tworzy kompleksowy zestaw narzdzi. To unikalne poczenie obu urzdze daje ogromne moliwoci w identyfikacji rde problemw zdrowotnych oraz w ich skutecznym leczeniu. Oferta Bio Strefy to nie tylko medycyna przyszoci, to terapia, ktra naprawd dziaa.',
    },
  ];

  return (
    <div className="faqContainer">
      {faqData.map((item, index) => (
        <Accordion key={index}>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls={`faq-content-${index}`}
          >
            <Typography variant="h6">{item.question}</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>{item.answer}</Typography>
          </AccordionDetails>
        </Accordion>
      ))}
    </div>
  );
};

export default FAQ;
