import BiorezonansPhoto from '../../../img/biorezonans.jpg';

const Biorezonans = () => {
  return (
    <>
      <div className="offer-description biorezonans">
        <div className="offer-description-text">
          Biorezonans ju od wielu lat jest wykorzystywany do kompleksowej
          diagnostyki organizmu, usuwania obcie ciaa (w tym metali cikich
          oraz rnego rodzaju patogenw takich jak grzyby, bakterie, wirusy czy
          pasoyty) oraz wspomagania leczenia chorb przewlekych. Aby
          zrozumie, w jaki sposb dziaa urzdzenie, naley mie wiadomo, e
          wszystko, co nas otacza jest energi. Wszystkie czsteczki materii,
          rwnie komrki w ludzkim ciele, pasoyty, wirusy, bakterie, toksyny,
          pyki emituj fale elektromagnetyczne. Zalenie od rodzaju czsteczki,
          materie posiadaj indywidualn dugo lub czstotliwo tych wanie
          fal. Nazywa si to wzorem czstotliwoci i jest on odczytywany przez
          narzdzia suce do biorezonansu. Nastpnie urzdzenia podczas
          terapii wzmacniaj podane czstotliwoci oraz neutralizuj te, ktre
          s chorobotwrcze. Celem terapii jest przywrcenie w organizmie
          prawidowych drga na poziomie komrkowym i komunikacji pomidzy
          komrkami, a wytumienie patologicznych, tak aby przywrci prawidow
          prac danego narzdu, ukadu lub caego organizmu. Terapia
          czstotliwociami pozwala na nieinwazyjne, komfortowe i nieobarczone
          skutkami ubocznymi zwalczanie patogenw takich jak pasoyty, bakterie,
          wirusy czy grzyby chorobotwrcze, jak rwnie wspomaganie pracy
          poszczeglnych narzdw, regulacj procesw zachodzcych w organizmie,
          wspomaganie procesw naprawczych, detoksykacyjnych, wygaszanie stanw
          zapalnych czy te wygaszanie dolegliwoci blowych. Gdy medycyna
          konwencjonalna pozostaje bezradna lub niesie skutki uboczne, warto
          sprbowa terapii biorezonansu, ktra w wielu przypadkach w szybki
          sposb gwarantuj popraw jakoci ycia nie powodujc przy tym adnych
          skutkw ubocznych.
        </div>
        <div className="biorezonans-photo">
          <img className="home-photo" src={BiorezonansPhoto} alt="harmony" />
        </div>
      </div>
    </>
  );
};

export default Biorezonans;
